import * as types from "../types";

const state = {
  currentExpression: []
};

const getters = {
  [types.GET_CURRENT_EXPRESSION]: state => {
    return state.currentExpression;
  }
};

const mutations = {
  [types.MUTATE_CURRENT_EXPRESSION]: (state, payload) => {
    state.currentExpression.push(payload);
  },
  [types.MUTATE_CLEAR_CURRENT_EXPRESSION]: state => {
    state.currentExpression = [];
  }
};

const actions = {
  [types.UPDATE_CURRENT_EXPRESSION]: ({ commit }, payload) => {
    commit(types.MUTATE_CURRENT_EXPRESSION, payload);
  },
  [types.CLEAR_CURRENT_EXPRESSION]: ({ commit }) => {
    commit(types.MUTATE_CLEAR_CURRENT_EXPRESSION);
  }
};

export default {
  state,
  mutations,
  actions,
  getters
};
