<template>
  <div>
    <v-text-field
      v-model="currentExpression"
      label="Expression"
      filled
      shaped
      :disabled="true"
    />
    <div v-text="feedbackErrorMessage(currentExpression)" />
  </div>
</template>
<script>
import * as types from "../store/types";

export default {
  computed: {
    currentExpression: {
      get() {
        return this.$store.getters[types.GET_CURRENT_EXPRESSION].join("");
      },
      set(currentExpression) {
        this.$store.dispatch(
          types.UPDATE_CURRENT_EXPRESSION,
          currentExpression
        );
      }
    }
  },
  methods: {
    feedbackErrorMessage(currentExpression) {
      try {
        this.isErrorClassActive = false;
        let evaluatedResult = eval(currentExpression);
        return evaluatedResult;
      } catch (err) {
        this.isErrorClassActive = true;
        return "Error in the expression";
      }
    }
  }
};
</script>
