<template>
  <v-container fluid>
    <v-row>
      <v-col class="action_element" @click="readInput(1)" cols="3">1</v-col>
      <v-col class="action_element" @click="readInput(2)" cols="3">2</v-col>
      <v-col class="action_element" @click="readInput(3)" cols="3">3</v-col>
      <v-col class="action_element" @click="readInput('+')" cols="3">+</v-col>
    </v-row>
    <v-row>
      <v-col class="action_element" @click="readInput(4)" cols="3">4</v-col>
      <v-col class="action_element" @click="readInput(5)" cols="3">5</v-col>
      <v-col class="action_element" @click="readInput(6)" cols="3">6</v-col>
      <v-col class="action_element" @click="readInput('-')" cols="3">-</v-col>
    </v-row>
    <v-row>
      <v-col class="action_element" @click="readInput(7)" cols="3">7</v-col>
      <v-col class="action_element" @click="readInput(8)" cols="3">8</v-col>
      <v-col class="action_element" @click="readInput(9)" cols="3">9</v-col>
      <v-col class="action_element" @click="readInput('/')" cols="3">/</v-col>
    </v-row>
    <v-row>
      <v-col class="action_element" @click="readInput(0)" cols="3">0</v-col>
      <v-col class="action_element" @click="updateHistory()" cols="3"
        >CLS</v-col
      >
      <v-col class="action_element" @click="readInput('%')" cols="3">%</v-col>
      <v-col class="action_element" @click="readInput('*')" cols="3">*</v-col>
    </v-row>
  </v-container>
</template>
<script>
import { mapActions } from "vuex";
import * as types from "../store/types";
export default {
  methods: {
    ...mapActions({
      readInput: types.UPDATE_CURRENT_EXPRESSION
    }),
    updateHistory() {
      if (this.$store.getters[types.GET_CURRENT_EXPRESSION].length !== 0) {
        try {
          let result = eval(
            this.$store.getters[types.GET_CURRENT_EXPRESSION].join("")
          );
          this.$store.dispatch(types.UPDATE_HISTORY, {
            expression: this.$store.getters[types.GET_CURRENT_EXPRESSION].join(
              ""
            ),
            result: result
          });
        } catch (err) {
          console.log("Invalid expression can't be commited");
        } finally {
          this.$store.dispatch(types.CLEAR_CURRENT_EXPRESSION);
        }
      }
    }
  }
};
</script>
<style scoped>
* {
  text-align: center;
}
.action_element {
  border: solid black 2px;
}
.action_element:hover {
  background-color: grey;
  cursor: pointer;
  color: white;
}
.action_element:active {
  background-color: black;
}
</style>
