<template>
  <v-data-table
    :headers="headers"
    :items="history"
    :items-per-page="5"
    class="elevation-2"
  ></v-data-table>
</template>
<script>
import * as types from "../store/types";
export default {
  data: () => ({
    headers: [
      {
        text: "Expression",
        value: "expression"
      },
      {
        text: "Result",
        value: "result"
      }
    ]
  }),
  computed: {
    history: {
      get() {
        return this.$store.getters[types.GET_HISTORY];
      },
      set(newExpression) {
        this.$store.dispatch(types.UPDATE_HISTORY, newExpression);
      }
    }
  }
};
</script>
<style scoped></style>
